import React, { useEffect, useState } from "react"
import SelectSearch, { SelectSearchOption } from 'react-select-search';

interface PickerViewProps {
    path: string;
    uid: string;
    title: string;
    onNameChange: (value) => void;
}

export function PickerView(props: PickerViewProps): JSX.Element {
    const [options, setOptions] = useState(undefined);
    const [value, setValue] = useState("");

    const getFile = async () => {
        const response = await fetch(props.path);
        if (!response.ok) {
            return; 
        }

        const text = await response.text();
        const lines: (string | SelectSearchOption)[] = text.split("\n")

        lines.forEach((element, idx) => {
            lines[idx] = { name: element as string, value: element as string};
        });

        setOptions(lines);
        props.onNameChange((lines[0] as SelectSearchOption).value);
    }

    const random = () => {
        const newValue = options[Math.floor(Math.random() * options.length)].value;
        setValue(newValue);
        props.onNameChange(newValue);
    }

    useEffect(() => {
        if (!options) {
            getFile();
        }
    });

    return (
        <div className="picker">
            <span>{props.title}</span>
            <SelectSearch options={options} value={value} search={true} onChange={props.onNameChange}></SelectSearch>
            <button onClick={random}>🐈</button>
        </div>
    );
}
